const selectedEventSelector = (state) => {
  const { events, selectedEventId } = state.calendar;

  if (selectedEventId) {
    return events.find((_event) => _event.id === selectedEventId);
  } else {
    return null;
  }
};

function ApplicationsCalendar() {
  const { t } = useTranslation();

  const theme = useTheme();
  const classes = useStyles();

  const token = window.localStorage.getItem('accessToken');
  const { enqueueSnackbar } = useSnackbar();

  const {user} = useAuth()
  const calendarRef = useRef(null);
  const mobile = useMediaQuery(theme.breakpoints.down('md'));
  const dispatch = useDispatch();
  const { events, isDrawerOpen, selectedRange } = useSelector(
    (state) => state.calendar
  );
  const [actualizar,setActualizar] = useState(false)
  const [espacioSelect,setEspacioSelect] = useState(null)
  const [eventLocal,setEventLocal] = useState([])
  const [paramDate,setParamDate] = useState()
  const selectedEvent = useSelector(selectedEventSelector);
  const [date, setDate] = useState(new Date());
  const [view, setView] = useState(mobile ? 'listWeek' : 'dayGridMonth');
  const [espacio,setEspacio] = useState([])
  const handleDateToday = () => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();

      calApi.today();
      setDate(calApi.getDate());
    }
  };


  const llamarReservas = async(espacio,fecha)=>{
    const result = await getReservaPorEspacio(espacio,fecha,token)

  console.log('llamadasssssss',result)
    if(result){
      let arr = []
      for(let val of result){
        let obj = { title: `${val.usuario && val.usuario.persona && val.usuario.persona.nombre} ${val.usuario && val.usuario.persona && val.usuario.persona.apellido}`, date: val.fechaInicio }

        arr.push(obj)

}
setEventLocal(arr)

    }
  
    console.log(result)
  }

  const changeView = (changedView) => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();

      calApi.changeView(changedView);
      setView(changedView);
    }
  };

  const handleDatePrev = () => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();

      calApi.prev();
      setDate(calApi.getDate());
    }
  };

  const handleDateNext = () => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();

      calApi.next();
      setDate(calApi.getDate());
    }
  };

  const handleAddClick = () => {
    dispatch(openDrawerPanel());
  };

  const handleRangeSelect = (arg) => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();

      calApi.unselect();
    }

    dispatch(selectRange(arg.start, arg.end));
  };

  const handleEventSelect = (arg) => {
    dispatch(selectEvent(arg.event.id));
  };

  const handleEventResize = async ({ event }) => {
    try {
      await dispatch(
        updateEvent(event.id, {
          allDay: event.allDay,
          start: event.start,
          end: event.end
        })
      );
    } catch (err) {
      console.error(err);
    }
  };

  const handleEventDrop = async ({ event }) => {
    try {
      dispatch(
        updateEvent(event.id, {
          allDay: event.allDay,
          start: event.start,
          end: event.end
        })
      );
    } catch (err) {
      console.error(err);
    }
  };

  const closeDrawer = () => {
    dispatch(closeDrawerPanel());
  };
  

  const actualizarCalendario = (data) => {
    setActualizar(data)
}

  const llamadaEspacio = async(param) => {
      const result = await getEspacioByClub(param,token)
      console.log('eeekekek',result)
    
      setEspacio(result)
  }

  useEffect(() => {
  
    if(user){
      llamadaEspacio(user.clubId)
  
    }

  
  },[])


  useEffect(() => {

    if(espacioSelect !== null){
      llamarReservas(espacioSelect,null)

      console.log('selectt',espacioSelect)
    }
 
  },[espacioSelect])


  useEffect(() => {


      if(actualizar === true){
        setActualizar(false)
      }
 
  },[actualizar])



  useEffect(() => {
    const calItem = calendarRef.current;

    if (calItem) {
      const calApi = calItem.getApi();
      const changedView = mobile ? 'listWeek' : 'dayGridMonth';

      calApi.changeView(changedView);
      setView(changedView);
    }
  }, [mobile]);

console.log('envntooo',events)

const handleDateClick = (arg) => { // bind with an arrow function
 setActualizar(true)
  console.log('aca la dataemiendi',arg.dateStr)
  setParamDate(arg.dateStr)
  setActualizar(false)
  
}
  return (
    <>
      <ContentWrapper title="Klubo | turnos">
        <PageTitleWrapper>
          <PageHeader espacio={espacio} handleCreateEvent={handleAddClick}  actualizarCalendario={(e) => { actualizarCalendario(e) } }/>
        </PageTitleWrapper>
        <Container maxWidth="lg">
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="stretch"
            spacing={3}
          >
               <Grid item xs={12} className="marginBuscador-turno">
       <Card sx={{ p: 1 }}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Box p={2}>
            <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel htmlFor="outlined-age-native-simple">Seleccionar un espacio</InputLabel>

              <Select

labelId="demo-simple-select-outlined-label"
id="demo-simple-select-outlined"
name="pais"
label="Seleccionar un espacio"
  onChange={(event)=>setEspacioSelect(event.target.value)}      
          >
         <MenuItem value="">
            <em>Elegir un deporte</em>
          </MenuItem>      
          {espacio.length > 0 && espacio.map((val,i)=>(
                  <MenuItem value={val.id} index={i}>{val.nombre}</MenuItem> 
                ))}
                  </Select>
                </FormControl>
            </Box>
          </Grid>
          </Grid>
          </Card>
          </Grid>
            <Grid item xs={12}>

               {espacioSelect === null ? 
               
               <Box display="flex" mt={4} justifyContent="center">

               <Typography variant="h3" gutterBottom>
               Selecciona un espacio
         </Typography>
                 </Box>
               :
               
               <Card>
               <Box p={3}>
                 <Actions
                   date={date}
                   onNext={handleDateNext}
                   onPrevious={handleDatePrev}
                   onToday={handleDateToday}
                   changeView={changeView}
                   view={view}
                 />
               </Box>
               <Divider />
               
               <FullCalendarWrapper>
                 <FullCalendar
                 locale={esLocale}
                   allDayMaintainDuration
                   initialDate={date}
                   initialView={view}
                   droppable
                   editable
                   dateClick={handleDateClick}
                   eventClick={handleEventSelect}
                   eventDrop={handleEventDrop}
                   dayMaxEventRows
                   eventResizableFromStart
                   eventResize={handleEventResize}
                   events={eventLocal}
                   headerToolbar={false}
                   height={800}
                   ref={calendarRef}
                   rerenderDelay={10}
                   select={handleRangeSelect}
                   actualizarx={actualizar}
                   selectable
                   weekends
                   plugins={[
                     dayGridPlugin,
                     timeGridPlugin,
                     interactionPlugin,
                     listPlugin
                   ]}
                 />
               </FullCalendarWrapper>
             </Card>
               
               } 
        

         
            </Grid>
          </Grid>
        </Container>
        <Box p={2}/>
      </ContentWrapper>
      <Drawer
        variant="temporary"
        anchor={theme.direction === 'rtl' ? 'left' : 'right'}
        onClose={closeDrawer}
        open={isDrawerOpen}
        elevation={9}
      >
        {isDrawerOpen && (
          <EventDrawer
            paramDate={paramDate}
            event={selectedEvent}
            range={selectedRange}
            onAddComplete={closeDrawer}
            onCancel={closeDrawer}
            onDeleteComplete={closeDrawer}
            onEditComplete={closeDrawer}
            isOpen={isDrawerOpen}
            espacio={espacioSelect}
            actualizarCalendario={(e) => { actualizarCalendario(e) }}
          />
        )}
      </Drawer>
    </>
  );
}

export default ApplicationsCalendar;
